<template>
  <div class="transaction-detail-table-5">
    <el-table
      :data="data"
      tooltip-effect="dark">
      <el-table-column
        prop="create_time_full"
        :label="$t('assets.transDetail.thead.table5.text1')">
      </el-table-column>
      <el-table-column
        prop="currency_mark"
        :label="$t('assets.transDetail.thead.table5.text2')">
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table5.text3')">
        <template slot-scope="scope">
          <!-- 转出数量 -->
          {{ scope.row.num }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table5.text4')">
        <template slot-scope="scope">
          <!-- 到账数量 -->
          {{ scope.row.actual }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table5.text5')">
        <template slot-scope="scope">
          {{ ti_id(scope.row.ti_id) }}
        </template>
      </el-table-column>
      <el-table-column
        prop="wallet_addr"
        :label="$t('assets.transDetail.thead.table5.text6')">
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table5.text7')">
        <template slot-scope="scope">
          {{ status(scope.row.status) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'transaction-detail-table-5',
    components: {
    },
    props: {
      data: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
      }
    },
    methods: {
      status(val) {
        if (val <= 0 || val > 4) return this.$t('assets.transDetail.tbody.typeFilter.text6')
        // type值为1,2,3,4时，显示提币中，成功，已撤销或已拒绝
        const map = [
          this.$t('assets.transDetail.tbody.outStatusFilter.text1'),
          this.$t('assets.transDetail.tbody.outStatusFilter.text2'),
          this.$t('assets.transDetail.tbody.outStatusFilter.text3'),
          this.$t('assets.transDetail.tbody.outStatusFilter.text4')
        ]
        return map[val - 1]
      },
      ti_id(val) {
        // 若没有提现ID，显示“无”
        if (!val) {
          return this.$t('assets.transDetail.tbody.remarkFilter.text1')
        } else {
          return val
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .transaction-detail-table-5{
    
  }
</style>



