<template>
  <div class="asset-trans-detail">
    <div class="auto-box">
      <el-card class="title">
        {{ $t(`assets.transDetail.title.text${listOption.type}`)}}
      </el-card>
      <div class="bottom">
        <div class="search">
          <el-form :model="listOption" inline>
            <el-form-item :label="$t('assets.transDetail.search.text1')">
              <el-select v-model="listOption.currency_mark" @change="handleCurrencySelect">
                <el-option v-for="item in currencyOptions" 
                  :key="item.id" 
                  :value="item.id" 
                  :label="item.currency_mark">          
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="$t('assets.transDetail.search.text2')">
              <el-date-picker
                v-model="listOption.search_time"
                clearable
                type="month"
                :placeholder="$t('assets.transDetail.search.text3')"
                :picker-options="pickerOptions"
                @change="getList">
              </el-date-picker>
            </el-form-item>
          </el-form>
        </div>
        <div class="tab">
          <el-tabs v-model="activeName" @tab-click="handleTabClick">
            <el-tab-pane :label="$t('assets.transDetail.tab.text1')" name="0">
            </el-tab-pane>
            <el-tab-pane :label="$t('assets.transDetail.tab.text2')" name="1">
            </el-tab-pane>
            <el-tab-pane :label="$t('assets.transDetail.tab.text3')" name="2">
            </el-tab-pane>
            <el-tab-pane :label="$t('assets.transDetail.tab.text4')" name="3">
            </el-tab-pane>
            <el-tab-pane :label="$t('assets.transDetail.tab.text5')" name="4">
            </el-tab-pane>
            <el-tab-pane :label="$t('assets.transDetail.tab.text6')" name="5">
            </el-tab-pane>
          </el-tabs>
        </div>
        <div class="table-wrap">
          <component
            v-loading="loading"
            :is="currentComponent"
            :data="tableData"
            :type="$route.query.type"></component>
        </div>
        <div class="pager">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="listOption.page"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="listOption.per_page_num"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalCount">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { getCtcOrOtcCurrency, tradeDetail } from '@/api/assets/detail'
  import Table1 from './components/DetailTable1'
  import Table2 from './components/DetailTable2'
  import Table3 from './components/DetailTable3'
  import Table4 from './components/DetailTable4'
  import Table5 from './components/DetailTable5'
  import Table6 from './components/DetailTable6'
  export default {
    name: 'asset-trans-detail',
    components: {
      Table1,
      Table2,
      Table3,
      Table4,
      Table5,
      Table6
    },
    data() {
      return {
        activeName: '0',
        loading: false,
        listOption: {
          currency_id: this.$route.query.currency_id,
          currency_mark: this.$route.query.currency_mark,
          type: this.$route.query.type === 'ctc' ? 1 : 2,
          search_time: '',
          detail_type: 0,
          page: 1,
          per_page_num: 10
        },
        currencyOptions: [],
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now()
          }
        },
        tableData: [],
        totalCount: 0
      }
    },
    mounted() {
      this.getCurrency()
      this.getList()
    },
    computed: {
      currentComponent() {
        const map = [
          Table1,
          Table2,
          Table3,
          Table4,
          Table5,
          Table6
        ]
        return map[this.activeName]
      }
    },
    methods: {
      getCurrency() {
        getCtcOrOtcCurrency({
          type: this.listOption.type
        }).then(res => {
          this.currencyOptions = res.data
        }).catch(() => {
  
        })
      },
      getList() {
        this.loading = true
        tradeDetail(this.listOption).then(res => {
          console.log('res-de', res)
          this.loading = false
          this.tableData = res.data.data
          this.totalCount = res.data.list_total
        }).catch(() => {
  
        })
      },
      handleCurrencySelect(val) {
        this.listOption.currency_id = val
        this.getList()
      },
      handleTabClick(tab, event) {
        this.listOption.detail_type = Number(this.activeName)
        this.getList()
      },
      handleSizeChange(val) {
        this.listOption.per_page_num = val
        this.getList()
      },
      handleCurrentChange(val) {
        this.listOption.page = val
        this.getList()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .asset-trans-detail {
    .auto-box{
      width: 1200px;
      margin: 0 auto;
      padding: 20px 30px;
      /deep/.el-card__body{
        padding: 12px;
      }
      /deep/.el-tabs__nav-wrap::after{
        display: none;
      }
      /deep/.el-tabs__header{
        margin: 0px;
      }
      .bottom{
        background-color: #fff;
        margin-top: 10px;
        padding: 30px;
        .pager{
          margin-top: 20px;
        }
      }
    }
  }
</style>



